<template>
  <div class="practice-wrapper">
    <div class="task">
      <div class="task-item">
        <p class="task-item-text" v-html="pages[pageIndex].html"></p>

        <template v-if="pages[pageIndex].type === TaskType.Practice">
          <div
            v-for="(line, index) in pages[pageIndex].cli"
            :key="line.id"
            class="task-item-cli"
            :class="{
              first: index === 0,
              last: index === pages[pageIndex].cli.length - 1
            }"
          >
            <span class="task-item-prefix">{{ line.prefix }}</span>
            <input type="text" class="task-item-input" v-model="line.input" />
          </div>
        </template>

        <template v-else-if="pages[pageIndex].type === TaskType.Custom">
          <component
            :ref="pages[pageIndex].id"
            :is="pages[pageIndex].component"
            :arr="pages[pageIndex].content"
          ></component>
        </template>

        <p
          v-if="isPassed !== null"
          class="task-item-result"
          :class="{
            passed: isPassed,
            failed: !isPassed
          }"
        >
          {{ statusText }}
        </p>

        <div class="task-item-options">
          <app-button
            :background="pages[pageIndex].button.background"
            :borderRadius="10"
            :width="150"
            :height="35"
            @click.native="pages[pageIndex].button.handler"
          >
            {{ pages[pageIndex].button.text }}
          </app-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ACTIONS, GETTERS, ROUTE_NAMES } from '@/constants'

import AppButton from '@/components/common/AppButton.vue'
import ClientServerTask from '@/components/common/PracticeTask/ClientServerTask.vue'

const TaskType = {
  Theory: 'theory',
  Practice: 'practice',
  Custom: 'custom'
}

export default {
  name: 'practice-task',
  components: {
    'app-button': AppButton
  },
  computed: {
    statusText() {
      return this.isPassed !== null
        ? this.isPassed
          ? ''
          : ''
        : null
    },
    isPracticePassed() {
      return this.$store.getters[GETTERS.GET_PRACTICE_RESULT]
    }
  },
  data() {
    return {
      TaskType,
      pageIndex: 0,
      isPassed: null,
      pages: [
        {
          id: 'page-custom-0',
          type: TaskType.Custom,
          component: ClientServerTask,
          check(answers) {
            const correctAnswers = ['3', '2', '1', '4']
            for (let i = 0; i < correctAnswers.length; i++) {
              if (answers[i] !== correctAnswers[i]) return false
            }
            return true
          },
          button: {
            background: 'green-plain',
            text: '',
            handler: this.onPracticeButtonClick
          },
          content: [
            {
              id: '3',
              text: '   DNS-'
            },
            {
              id: '2',
              text: '  DNS-'
            },
            {
              id: '1',
              text: '   IP-'
            },
            {
              id: '4',
              text: '  '
            }
          ]
        },
        {
          id: 'page-0',
          type: TaskType.Theory,
          html: `
            ,   DHCP ,    IPv4 
             (),       .
             ,  IP     
            ,   ,   , 
              ,      
             .   ,   <span class="task-item-text-code">ip dhcp
            excluded-address</span>.        
             ,    ,     
            .
          `,
          button: {
            background: 'green-plain',
            text: '',
            handler: this.onTheoryButtonClick
          }
        },
        {
          id: 'page-1',
          type: TaskType.Practice,
          html: `
            <b>:</b>    
            <span class="task-item-text-code">192.168.1.1</span>   
            <span class="task-item-text-code">192.168.1.2</span> 
            <span class="task-item-text-code">192.168.1.9</span>
          `,
          cli: [
            {
              id: 'page-1-cli-0',
              prefix: 'Router(config)#',
              input: '',
              correct: 'ip dhcp excluded-address 192.168.1.1'
            },
            {
              id: 'page-1-cli-1',
              prefix: 'Router(config)#',
              input: '',
              correct: 'ip dhcp excluded-address 192.168.1.2 192.168.1.9'
            }
          ],
          button: {
            background: 'green-plain',
            text: '',
            handler: this.onPracticeButtonClick
          }
        },
        {
          id: 'page-2',
          type: TaskType.Theory,
          html: `
             DHCP       , 
             .     
            <span class="task-item-text-code">ip dhcp pool [_]</span>.
                   
            <span class="task-item-text-code"
              >network [_][/_]</span
            >
                    
            <span class="task-item-text-code"
              >default-router[_default_gateway]</span
            >
                 (   8 ). <br />
                  ,  DNS
             (
            <span class="task-item-text-code">dns-server []</span>), 
             (
            <span class="task-item-text-code">domain-name []</span>), NetBIOS
            WINS  (
            <span class="task-item-text-code">netbios-name-server[]</span>), 
                 (
            <span class="task-item-text-code"
              >lease [___]</span
            >,   ,    ,   ). 
                1 .
          `,
          button: {
            background: 'green-plain',
            text: '',
            handler: this.onTheoryButtonClick
          }
        },
        {
          id: 'page-3',
          type: TaskType.Practice,
          html: `
            <b>:</b>
            <ul>
              <li>
                1.     <span class="task-item-text-code">UISI-POOL-1</span>
              </li>
              <li>
                2.       .   <span class="task-item-text-code">192.168.1.0</span>
              </li>
              <li>
                3.     <span class="task-item-text-code">192.168.1.1</span>
              </li>
              <li>
                4.  <span class="task-item-text-code">192.168.1.2</span>   DNS-
              </li>
              <li>
                5.  <span class="task-item-text-code">uisi.ru</span>,   
              </li>
              <li>
                6.    12 
              </li>
            </ul>
          `,
          cli: [
            {
              id: 'page-2-cli-0',
              prefix: 'Router(config)#',
              input: '',
              correct: 'ip dhcp pool UISI-POOL-1'
            },
            {
              id: 'page-2-cli-1',
              prefix: 'Router(dhcp-config)#',
              input: '',
              correct: 'network 192.168.1.0 255.255.255.0'
            },
            {
              id: 'page-2-cli-2',
              prefix: 'Router(dhcp-config)#',
              input: '',
              correct: 'default-router 192.168.1.1'
            },
            {
              id: 'page-2-cli-3',
              prefix: 'Router(dhcp-config)#',
              input: '',
              correct: 'dns-server 192.168.1.2'
            },
            {
              id: 'page-2-cli-4',
              prefix: 'Router(dhcp-config)#',
              input: '',
              correct: 'domain-name uisi.ru'
            },
            {
              id: 'page-2-cli-5',
              prefix: 'Router(dhcp-config)#',
              input: '',
              correct: 'lease 0 12'
            }
          ],
          button: {
            background: 'green-plain',
            text: '',
            handler: this.onPracticeButtonClick
          }
        }
      ]
    }
  },
  methods: {
    onTheoryButtonClick() {
      this.pageIndex++
      this.isPassed = null
    },
    onPracticeButtonClick() {
      const page = this.pages[this.pageIndex]
      const isLastPage = this.pageIndex === this.pages.length - 1
      let isTaskCorrect = false

      if (this.isPassed) {
        return isLastPage ? this.onComplete() : this.onTheoryButtonClick()
      }

      if (page.type === TaskType.Custom) {
        if (page.check(this.$refs[page.id].list.map(item => item.id))) {
          isTaskCorrect = true
        }
      } else {
        if (
          !page.cli.every(line => {
            line.input = line.input.replace(/\s{2,}/g, ' ').trim()
            return !!line.input
          })
        ) {
          return
        }

        const cli = page.cli
        const counter = cli.reduce(
          (acc, val) => (val.input === val.correct ? acc + 1 : acc),
          0
        )

        if (cli.length === counter) {
          isTaskCorrect = true
        }
      }

      if (isTaskCorrect) {
        page.button.text = isLastPage ? '' : ''
        this.isPassed = true
      } else {
        this.isPassed = false
      }
    },
    onComplete() {
      this.$store
        .dispatch(ACTIONS.SET_PRACTICE_RESULT)
        .then(this.$router.push({ name: ROUTE_NAMES.HOME }))
    }
  },
  created() {
    if (this.isPracticePassed) {
      return this.$router.push({ name: 'home' })
    }
  }
}
</script>

<style lang="scss">
@import '@/assets/transition.scss';

$codeBackground: rgba(0, 0, 0, 0.08);
$taskWidth: 777px;
$borderRadius: 10px;

.practice {
  &-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;

    html {
      overflow-y: hidden;
    }
  }
}

.task {
  max-width: $taskWidth;
  background-color: rgba(255, 255, 255, 0.95);
  border-radius: 25px;
  box-shadow: 0 10px 15px 0 rgba(0, 0, 0, 0.15);
  &-item {
    padding: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    &-text {
      &-code {
        padding: 0 10px;
        border-radius: 5px;
        background-color: $codeBackground;
        font-family: monospace;
      }
    }
    &-cli {
      display: flex;
      flex-flow: row nowrap;
      width: 100%;
      padding: 5px 10px;
      background-color: $codeBackground;
      &.first {
        margin-top: 20px;
        border-top-right-radius: $borderRadius;
        border-top-left-radius: $borderRadius;
      }
      &.last {
        border-bottom-right-radius: $borderRadius;
        border-bottom-left-radius: $borderRadius;
      }
    }
    &-prefix {
      width: 132px;
      display: inline-block;
      margin-right: 5px;
      font-size: 12px;
      font-family: monospace;
      text-align: right;
    }
    &-input {
      width: 75%;
      display: inline-block;
      font-size: 12px;
      font-family: monospace;
      border: none;
      background-color: transparent;
    }
    &-result {
      margin-top: 20px;
      &.passed {
        color: rgb(50, 150, 50);
      }
      &.failed {
        color: rgb(200, 50, 50);
      }
    }
    &-options {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      margin-top: 20px;
    }
  }
}

@media (min-width: 799px) {
  .task {
    width: $taskWidth;
  }
}

@media (max-width: 650px) {
  .task {
    &-item {
      &-input {
        width: 50%;
      }
    }
  }
}

@media (max-width: 600px) {
  .practice {
    &-wrapper {
      align-items: flex-end;
    }
  }
  .task {
    width: 100%;
    background-color: rgba(255, 255, 255, 0.95);
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.15);
    &-item {
      &-input {
        width: 45%;
      }
    }
  }
}

@media (max-width: 321px) {
  .practice {
    &-wrapper {
      align-items: flex-end;
    }
  }
  .task {
    width: 100%;
    background-color: rgba(255, 255, 255, 0.95);
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.15);
    font-size: 10px;
    &-item {
      &-prefix,
      &-input {
        font-size: 10px;
      }
    }
  }
}
</style>
